

import setValueForStyles from './../../setValueForStyles';
import StyleSheet from './../../../apis/StyleSheet/StyleSheet.web';

let setNativeProps = function(node, props, component) {
    for (let name in props) {
        if (name === 'style') {
            setValueForStyles(node,StyleSheet.fix(props[name]), component);
        } else if (name === 'pointerEvents'){
            // ext的pointerEvents应放在style中而不是作为html的属性
            var style = {
              ...props.style,
              pointerEvents:props['pointerEvents']
            }
            setValueForStyles(node,style, component);
        } else{
           node.setAttribute(name, props[name]);
           if (node.props) node.props[name] = props[name] // 也必须更新到props上，不然input不能用
        }
    }
}

export  default setNativeProps
